body {
  color: #fff;
  background-color: #000;
  font-family: Arial, Helvetica, sans-serif;
}

.field {
  border-radius: min(1vh, 1vw);
  margin: 10vh 10vw 0;
}

h1 {
  margin: 2vw 10vw;
}

div.margin {
  word-wrap: break-word;
  -webkit-hyphens: auto;
  hyphens: auto;
  margin: 0 10vw;
}

select.margin {
  margin: 1em 10vw;
}

.stars {
  margin: 0 10vw;
  display: grid;
}

.field-glow {
  animation: 2s infinite large-pulse-animation;
}

.field5 {
  grid-template-rows: repeat(5, min(10vh, 10vw));
  grid-template-columns: repeat(5, min(10vh, 10vw));
  gap: min(.3vh, .3vw);
  display: grid;
}

.field6 {
  grid-template-rows: repeat(6, min(10vh, 10vw));
  grid-template-columns: repeat(6, min(10vh, 10vw));
