  gap: min(.3vh, .3vw);
  display: grid;
}

.box {
  width: min(10vh, 10vw);
  height: min(10vh, 10vw);
  color: #fff;
  background-color: beige;
  border-radius: min(2vh, 2vw);
  place-content: center;
  animation: 50ms linear driveIn;
  display: grid;
}

